.navbar{
    background-color: #274b69;
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar h1{
    font-family: "Sacramento";
    font-size: 2rem;
    color: #fff;
    margin: 0;
}

.navbar div a{
    color: #fff;
    margin: 10px;
    padding: 10px;
    transition: .2s;
    border-radius: 10px;
}

.navbar div a:hover{
    background-color: #85a1c1;
}

.navMenu{
    display: none;
}


@media screen and (max-width : 728px) {
    .navbar {
        display: none;        
    }
    
    .navMenu{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        align-content: center;
        width: 300px;
        height: 100vh;
        background-color: #274b69;
        position: fixed;
        top: 0;
        left: -100%;
        text-align: center;
        margin: 0;
        transition: all linear .3s;
    }

    .navMenu div{
        width: 100%;
        text-align: center;
        margin: 20px 5px;
        padding: 15px;
    }

    .navMenu div a{
        padding: 20px 40px;
        color: #fff;
        border-radius: 10px;
        transition: all .2s linear;
    }

    .navMenu div a:hover{
        background-color: #85a1c1;
    }

    .navMenu.active {
        left: 0;
    }

    .copyRight{
        font-size: 12px;
        position: absolute;
        bottom: 0;
        color: #b4b4b4;
    }
}