.burgerWrapper{
    display : none;
}

@media screen and (max-width : 728px){
    .burgerWrapper{
        width : 3rem;
        height : 3rem;
        padding: 15px 10px;
        position : fixed;
        top : 10px;
        left : 20px;
        z-index : 5;
        cursor : pointer;
        display : flex;
        flex-wrap : wrap;
        justify-content : center;
        align-items : center;
        align-content : space-between;
    }
    
    .burgerWrapper span{
        width : 2rem;
        height : 2.1px;
        border-radius : 10px;
        background-color : #000;
        transition: .2s .1s all linear;
    }

    .burgerWrapper.active span{
        background-color: #fff;
    }
    
    .burgerWrapper.active span:nth-child(1){
        transform: rotate(-45deg) translate(-5px , 7px);
    }

    .burgerWrapper.active span:nth-child(3){
        transform: rotate(45deg) translate(-5px , -6px);
    }

    .burgerWrapper.active span:nth-child(2){
        opacity: 0;
    }


}